.info {
  display: flex;
  gap: 65px;
  margin-top: -30px;

  @include vp-1199 {
    margin-top: 0;
    gap: 30px;
  }
}

.info__inner {
  display: grid;
  grid-template-columns: 132px auto;
  margin-bottom: 45px;

  @include vp-1199 {
    grid-template-columns: 113px auto;
    margin-bottom: 35px;
  }

  @include vp-767 {
    margin-bottom: 22px;
  }
}

.info__wrapper {
  width: 34%;
  padding-top: 112px;

  @include vp-1199 {
    width: 48%;
    padding-top: 50px;
  }

  @include vp-767 {
    width: 100%;
    padding-top: 45px;
  }
}

.info__content {
  display: flex;
  flex-direction: column;
  text-align: right;
}

.info__content p {
  margin: 0;
  padding: 0;
  margin-right: 50px;
  font-weight: 600;
  font-size: 43px;
  line-height: 58px;
  text-transform: uppercase;
  letter-spacing: 0.03em;

  @include vp-1199 {
    margin-right: 5px;
    font-size: 30px;
    line-height: 41px;
  }

  @include vp-767 {
    font-size: 24px;
    line-height: 34px;
  }
}

.info__content h1 {
  margin: 0;
  margin-top: 15px;
  padding: 0;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-transform: uppercase;
  letter-spacing: 0.05em;

  @include vp-1199 {
    margin-top: 7px;
    font-size: 72px;
    line-height: 86px;
    text-transform: none;
  }

  @include vp-767 {
    margin-left: -10px;
    margin-top: 13px;
  }
}

.info__inner ul {
  margin: 0;
  padding: 0;
  padding-top: 193px;
  list-style: none;

  @include vp-1199 {
    padding-top: 135px;
  }

  @include vp-767 {
    padding-top: 123px;
  }
}

.info__inner li {
  font-family: $ff-roboto-condensed;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  text-align: right;
}

.info__button {
  display: block;
  width: 59%;
  margin-left: 163px;
  font-family: $ff-roboto-condensed;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  padding: 20px 50px;
  text-align: center;
  text-transform: uppercase;
  background-color: transparent;
  color: $color-default-white;
  border: 4px solid $color-default-white;

  @include vp-1199 {
    margin-left: 135px;
    padding: 20px 30px;
  }

  @include vp-767 {
    width: 100%;
    margin-left: 0;
  }

  &:hover,
  &:focus-visible {
    background-color: $color-default-white;
    color: $color-default-black;
    outline: none;
  }

  &:active {
    opacity: 0.5;
  }
}

.info__image-wrapper {
  max-width: 500px;

  @include vp-1199 {
    max-width: 324px;
    height: auto;
  }
}

.info__image {
  background-color: $color-cerulean;

  @include vp-1199 {
    aspect-ratio: 324/390;
    object-fit: cover;
  }

  @include vp-767 {
    display: none;
  }
}
